import React, { useContext, useEffect } from 'react';
import { AppContext } from './App';

function GameOver() {
  const {
    gameOver,
    correctWord,
    currAttempt,
    copyAlertControl,
    setCopyAlertControl,
    fadeInAnimation,
    setFadeInAnimation,
    fadeOutAnimation,
    setFadeOutAnimation,
  } = useContext(AppContext);

  useEffect(() => {
    setFadeInAnimation('fade-in-animation');
    let fadeOutAnimation = setTimeout(() => {
      setFadeOutAnimation('fade-out-animation');
    }, 2000);
    let timerAlert = setTimeout(() => {
      setCopyAlertControl(false);
    }, 3000);
    return () => {
      clearTimeout(timerAlert);
      clearTimeout(fadeInAnimation);
      clearTimeout(fadeOutAnimation);
      setFadeInAnimation('');
      setFadeOutAnimation('');
    };
  }, [copyAlertControl]);

  const copyResult = () => {
    const letterDiv = document.getElementsByClassName('letter');
    const finalAttempt = gameOver.guessedWord ? currAttempt.attempt : 'X';
    let result = [[` ${finalAttempt}/6`], ['']];
    for (let i = 0; i < letterDiv.length; i++) {
      if (i % 6 === 0 && i < 6 * currAttempt.attempt) {
        result.push('');
      }
      if (letterDiv[i].id === 'correct') {
        result.push('');
      }
      if (letterDiv[i].id === 'almost') {
        result.push('');
      }
      if (letterDiv[i].id === 'error') {
        result.push('');
      }
    }
    result = result.join('');
    result = result.split('');
    result.push([], ['yujibug.github.io/muhanwordle']);
    result = result.join('\r\n');
    doCopy(result);
  };

  const doCopy = (text) => {
    //  1.
    if (navigator.clipboard) {
      // (IE  ,  66   .)
      navigator.clipboard
        .writeText(text)
        .then(() => {
          setCopyAlertControl(true);
        })
        .catch(() => {
          alert('  .');
        });
    } else {
      //  2.
      if (!document.queryCommandSupported('copy')) {
        return alert('   .');
      }

      //  3.
      const textarea = document.createElement('textarea');
      textarea.value = text;
      textarea.style.top = 0;
      textarea.style.left = 0;
      textarea.style.position = 'fixed';

      //  4.
      document.body.appendChild(textarea);
      // focus() ->   
      textarea.focus();
      // select() ->       
      textarea.select();
      //  5.
      document.execCommand('copy');
      //  6.
      document.body.removeChild(textarea);
      setCopyAlertControl(true);
    }
  };

  return (
    <div className='gameover-modal'>
      {copyAlertControl && (
        <div className='alert-wrapper'>
          <div className={`alert ${fadeInAnimation} ${fadeOutAnimation}`}>
             
          </div>
        </div>
      )}
      <div className='gameover-modal-body'>
        <h1>{gameOver.guessedWord ? '!' : ''}</h1>
        <h1> : '{correctWord}'</h1>
        <div className='btn-wrapper'>
          <button
            className='btn'
            onClick={() => {
              window.location.reload();
            }}
          >
              
          </button>
        </div>
        <div className='btn-wrapper'>
          <button className='btn' onClick={copyResult}>
             
          </button>
        </div>
      </div>
    </div>
  );
}

export default GameOver;
